.inputImg {
  position: relative;

  overflow: hidden;

  fill: gray;

  border-radius: 4px;
  outline: 2px solid gray;

  transition: all var(--timing-function__main);

  &:hover {
    outline-color: var(--brand_clr__1);
    fill: var(--brand_clr__1);
    transform: scale(1.25);
    z-index: 10;
    & .labelInner {
      border-color: var(--brand_clr__1);
    }
  }

}
.inputLabel {
  display: flex;
  align-items: center;
  justify-content: center;

  min-height: 70px;
  max-height: 120px;
  min-width: 70px;
  max-width: 120px;

  background-color: #fff;
  transition: all var(--timing-function__main);
}
