.labelInner {
  display: flex;
  align-items: center;
  justify-content: center;

  // width: 80%;
  // height: 80%;


  border-radius: 50%;
  border: 2px solid gray;
}
.labelInnerImg {
  display: block;
  // position: absolute;

  min-height: 70px;
  width: 70px;
}
