.Backdrop {
  display: flex;
  align-items: center;
  justify-content: center;

  position: fixed;
  top: 0;
  left: 0;
  z-index: 1300;

  width: 100%;
  height: 100%;

  background-color: rgba(0, 0, 0, 0.5);
}

.Modal {
  position: absolute;
  top: 50%;
  left: 50%;

  transform: translate(-50%, -50%);

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;

  min-width: 250px;

  min-height: 250px;

  border-radius: 4px;
  background-color: rgba(255, 255, 255, 0.5);
}
@media screen and (max-width: 768px) {
  .Modal {
    max-width: 98%;
    max-height: 98%;
  }
}
@media screen and (min-width: 768px) {
  .Modal {
    max-width: 700px;
    max-height: 98%;
  }
}

.closeModal {
  position: absolute;
  top: 8px;
  right: 8px;
  z-index: 15;

  font-size: 16px;
  color: #fff;

  width: 28px;
  height: 28px;
  padding: 4px;

  cursor: pointer;

  border-style: none;
  background-color: transparent;
  border-radius: 4px;
  border: 1px solid transparent;

  background-color: rgba(0, 0, 0, 0.5);
}
